/* src/components/Players.css */
table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f4f4f4;
  cursor: pointer;
}

.sorted-asc::after {
  content: " ↑";
}

.sorted-desc::after {
  content: " ↓";
}

.gp-column {
  width: 150px;
}

.stats-column {
  width: 100px;
}
